/** @jsx jsx */
import { h, Fragment, Component } from "preact";
import { observer } from "mobx-react";
import { useMemo, useState, useEffect } from 'preact/hooks';
import { jsx, css } from '@emotion/react';

import classnames from 'classnames';

import { MediaQuery } from '../Helpers/MediaQuery'
import { filters } from '@searchspring/snap-toolbox';

import {
  ThemeProvider,
  defaultTheme,
  FilterSummary,
  Facet,
  FacetSlider,
  FacetGridOptions,
  FacetPaletteOptions,
  FacetListOptions,
  FacetHierarchyOptions,
  StoreProvider,
  withStore,
  withTheme,
  withController,
  ControllerProvider,
  Icon,
} from '@searchspring/snap-preact-components';

import './sidebar.css'

@observer
export class Sidebar extends Component {
  render() {
    const store = this.props.controller.store;

    return (
      <ThemeProvider theme={defaultTheme}>
        <ControllerProvider controller={this.props.controller}>
          <StoreProvider store={store}>
            <SidebarContents />
          </StoreProvider>
        </ControllerProvider>
      </ThemeProvider>
    );
  }
}

@withController
@withStore
@observer
export class SidebarContents extends Component {
  render() {
    const {
      filters,
      facets,
    } = this.props.store;

    const leftColumnLayout = this.props.leftColumnLayout;
    let displayIconFilters = ["mfield_cql_arch_height", "mfield_cql_insole_thickness", "mfield_cql_underfoot_cushioning"];

    //Check if French
    if(window.Shopify.locale === "fr") {
      displayIconFilters = ["mfield_french_arch_height", "mfield_french_insole_thickness", "mfield_french_underfoot_cushioning"];
    }
  
    const isTierOne =  window.location.pathname.indexOf('collections/all') > -1;

    //For tags, trim out french prefix
    //Mobile active filters
    filters.forEach((filter, index) => {
      if(filter.label.includes("french")) {
        filters[index].value.label = filters[index].value.label.replace("french__", "");
      }
      
      filters[index].value.label = filters[index].value.label.split('-').map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(' ');
    });

    return (
      <div class={ leftColumnLayout ? "ss-sidebar-container-left-column" : "ss-sidebar-container"}>
        <FilterSummary filters={filters} controller={this.props.controller} />

        <MediaQuery query="(min-width: 990px)">
          <SidebarHeadings />
        </MediaQuery>

        <div className='ss__facets'>
          {facets.map((facet, index) => {
            const displayIcon = displayIconFilters.indexOf(facet.field) > -1;
            let hideActivity = !isTierOne && facet.label.toLowerCase() === "activity";

            //TODO: Hook this "Activity" check up to a metafield or theme setting instead of hard code.
            if(window.Shopify.locale === "fr") {
              hideActivity = !isTierOne && facet.label.toLowerCase() === "activité";
            }

            return facet.display === 'none' || hideActivity ? null : (
              <>
                <CustomFacet displayIcon={displayIcon} facet={facet} disableOverflow index={index}/>
                <hr className="facet__sidebar-separator"/>
              </>
            );
          })}
        </div>
      </div>
    );
  }
}
/*
Shopify.page_title -> window.Resources.searchspring.toolbar.sidebarHeadingFilter
    window.Resources.searchspring.toolbar.collectionTagline -> collection.metafields.cql.collection_tagline_text
    window.Resources.searchspring.toolbar.collectionTaglineLinkText -> collection.metafields.cql.collection_tagline_link_text
    window.Resources.searchspring.toolbar.collectionTaglineLinkUrl -> collection.metafields.cql.collection_tagline_link_url
*/
