                      <path id="Vector_2" d="M10 8.99998C9.93442 9.00078 9.86941 8.98785 9.80913 8.96201C9.74885 8.93618 9.69465 8.89802 9.65 8.84998C9.45 8.64998 9.45 8.33998 9.65 8.13998L12.8 4.98998L9.65 1.83998C9.45 1.63998 9.45 1.32998 9.65 1.12998C9.85 0.92998 10.16 0.92998 10.36 1.12998L13.86 4.62998C14.06 4.82998 14.06 5.13998 13.86 5.33998L10.36 8.83998C10.26 8.93998 10.13 8.98998 10.01 8.98998L10 8.99998Z" fill="#0D0D0D"/>
